/*
** Apply styles to the parent element
*/
.Words {
  padding-top: 100px;
  padding-bottom: 230px;
  margin-left: -165px;
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  font-size: 68px;
  font-weight: 900;
  letter-spacing: -2px;
  text-transform: uppercase;
  transform: translate3d(0, 0, 0);
  -webkit-font-smoothing: antialiased;
  -webkit-font-kerning: normal;
  -webkit-text-size-adjust: 100%; }

/*
** Apply common styles to each line of text
*/
.Words-line {
  height: 50px;
  overflow: hidden;
  position: relative; }
  .Words-line:nth-child(odd) {
    transform: skew(60deg, -30deg) scaleY(0.66667); }
  .Words-line:nth-child(even) {
    transform: skew(0deg, -30deg) scaleY(1.33333); }
  .Words-line:nth-child(1) {
    left: 29px; }
  .Words-line:nth-child(2) {
    left: 58px; }
  .Words-line:nth-child(3) {
    left: 87px; }
  .Words-line:nth-child(4) {
    left: 116px; }
  .Words-line:nth-child(5) {
    left: 145px; }
  .Words-line:nth-child(6) {
    left: 174px; }

/*
** Fine-grained text styles
*/
p.cartel {
  height: 50px;
  line-height: 45px;
  padding: 0 10px;
  transition: all .4s ease-in-out;
  transform: translate3d(0, -15px, 0);
  vertical-align: top;
  white-space: nowrap; }

/*
** The hover interaction
*/
.Words:hover p {
  transform: translate3d(0, -65px, 0); }

@media (max-width: 900px) {
  .Words {
    padding-top: 65px;
    padding-bottom: 40px;
    margin-left: -187px;
    font-size: 18px;
    font-weight: 900;
    letter-spacing: 0px; }
  .Words-line {
    height: 30px; }
  p.cartel {
    height: 45px;
    line-height: 50px;
    padding: 0 10px; }
  .Words p {
    animation: escaleras 4.5s ease-in-out;
    animation-iteration-count: infinite; }
  @keyframes escaleras {
    0% {
      transform: translate3d(0, -15px, 0); }
    50% {
      transform: translate3d(0, -55px, 0); }
    100% {
      transform: translate3d(0, -15px, 0); } } }

@keyframes scale {
  0% { }
  50% {
    transform: scale(1.2); }
  100% { } }

@keyframes heroAnimation {
  0% {
    opacity: 0;
    transform: translateY(100%); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes homeAnimation {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-image: radial-gradient(#343282, #060541);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  color: #FFF;
  font-family: 'Orbitron'; }

.heroContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 28vh; }

.logo {
  fill: white; }

.logoHero {
  width: 95px;
  margin-right: 15px; }

.heroH2 {
  font-size: 56px;
  font-weight: 900;
  letter-spacing: 3px;
  margin-bottom: 70px;
  animation: heroAnimation .8s ease-in-out .1s; }

.heroH3 {
  font-size: 36px;
  font-weight: 900;
  padding: 12px 35px;
  background-color: rgba(6, 5, 65, 0.3);
  border-radius: 35px;
  transition: all .2s;
  animation: heroAnimation .8s ease-in-out .2s; }

.heroH3:hover {
  background-color: rgba(6, 5, 65, 0.8);
  border: 1px solid white;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  cursor: pointer; }

.heroH3:active {
  transform: translateY(-1px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer; }

.hero {
  font-size: 90px;
  font-weight: 900;
  letter-spacing: 0px;
  margin-bottom: 70px;
  animation: heroAnimation .8s ease-in-out; }

.home {
  width: 99vw;
  height: 100vh;
  padding: 65px;
  animation: homeAnimation .5s ease-in-out;
  display: flex;
  flex-direction: column; }

.logoHome {
  display: flex;
  justify-content: center;
  font-size: 70px;
  font-weight: 900;
  letter-spacing: 0px;
  width: 24%; }

.logoHome .logoHero {
  width: 75px;
  margin-right: 15px; }

.header {
  display: flex;
  margin-bottom: 75px; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 75%;
  text-transform: uppercase;
  font-size: 35px;
  font-weight: 900;
  letter-spacing: 3px;
  padding-left: 60px;
  padding-right: 40px;
  padding-bottom: 12px; }

.main {
  display: flex;
  flex: 1; }

.footer {
  align-self: center;
  justify-self: flex-end;
  font-size: 36px;
  padding-bottom: 30px; }

.logos {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  padding: 30px;
  height: 630px; }

.areaGrid {
  grid-area: 3 / 1 / 4 / 3; }

.logoSVG {
  filter: invert();
  width: 150px;
  margin: 0px 58px; }

.logoSVG:hover {
  cursor: pointer; }

.logoSVGPasos {
  filter: invert();
  width: 220px;
  margin-bottom: 50px; }

.texto {
  padding: 15px 50px;
  font-size: 22px; }

.textoHome {
  margin-bottom: 65px;
  letter-spacing: 5px;
  font-weight: 700;
  line-height: 150%;
  text-indent: 58px;
  font-family: Lato; }

.botonMenu:hover {
  border-radius: 10px;
  transform: translateY(-1px);
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.2);
  cursor: pointer;
  transition: all .5s; }

.botonMenu:active {
  transform: translateY(0px);
  transition: all .1s !important;
  box-shadow: none; }

.contenedorMarketing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: homeAnimation .5s ease-in-out; }

.contenedorPasos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: homeAnimation .5s ease-in-out; }

.pasos {
  display: flex;
  justify-content: center;
