  margin-bottom: 65px; }

.moviles {
  font-size: 35px;
  font-weight: 900;
  letter-spacing: 3px;
  margin-bottom: 80px;
  margin-top: 80px; }

.chatBot {
  font-size: 35px;
  font-weight: 900;
  letter-spacing: 3px;
  margin-bottom: 80px;
  margin-top: 80px; }

.contenedorDesign {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: homeAnimation .5s ease-in-out; }

.contenedorEmpresa {
  display: flex;
